"use client";
import { useState } from "react";

export default function VoiceChat() {
  const [isListening, setIsListening] = useState(false);
  const [transcript, setTranscript] = useState("");

  const startListening = () => {
    const recognition = new (window.SpeechRecognition || window.webkitSpeechRecognition)();
    recognition.lang = "pt-BR";
    recognition.onresult = (event) => {
      setTranscript(event.results[0][0].transcript);
    };
    recognition.start();
    setIsListening(true);
    recognition.onend = () => setIsListening(false);
  };

  return (
    <div className="p-6">
      <h1 className="text-2xl font-bold mb-4">ðŸŽ¤ Chat de Voz (Simulado)</h1>
      <button onClick={startListening} disabled={isListening} className="bg-blue-500 text-white py-2 px-4 rounded mb-2">
        {isListening ? "Ouvindo..." : "Iniciar Microfone"}
      </button>
      <p>Ãšltima fala: {transcript}</p>
    </div>
  );
}
